<template>
  <form onsubmit="return false;" class="border p-5 mb-5" v-if="IsletmelerLoadOk">
    <div class="d-flex align-items-center justify-content-center mb-5" v-if="!Ekle">
      <p class="font-weight-bold m-0 text-white">Düzenlemeyi Yasakla:</p>
      <toggle-button
        v-model="Edit"
        :labels="{ checked: 'Açık', unchecked: 'Kapalı' }"
        :width="122"
        :height="44"
        :margin="0"
        :font-size="18"
        :color="{checked: '#1e90ff', unchecked: 'tomato', disabled: '#CCCCCC'}"
      />
      <button class="btn btn-danger px-5 py-2 ml-5" @click="Sil">Sil</button>
    </div>

    <div class="form-group">
      <label>Email Adresi</label>
      <input
        type="email"
        class="form-control"
        placeholder="Email"
        v-model="Isletme.Email"
        :disabled="Edit"
      />
    </div>

    <div class="form-group">
      <label>Kullanıcı Adı</label>
      <input
        type="text"
        class="form-control"
        placeholder="Kullanıcı Adı"
        v-model="Isletme.KullaniciAdi"
        :disabled="Edit"
      />
    </div>

    <div class="form-group">
      <label>Şifresi</label>
      <input
        type="password"
        class="form-control"
        placeholder="Şifre"
        v-model="Isletme.Sifre"
        :disabled="Edit"
      />
    </div>

    <div class="form-group">
      <label>Adı</label>
      <input
        type="text"
        class="form-control"
        placeholder="Adı"
        v-model="Isletme.IsletmeAdi"
        :disabled="Edit"
      />
    </div>

    <div class="form-group">
      <label>Adresi</label>
      <input
        type="text"
        class="form-control"
        placeholder="Adres"
        v-model="Isletme.Adres"
        :disabled="Edit"
      />
    </div>

    <button
      type="submit"
      class="ml-5 btn btn-success px-5 py-2 font-weight-bold"
      @click="Kaydet"
    >Kaydet</button>

    <div v-if="!this.Ekle">

    <card Title="Çorbalar" :SomeArray=Isletme.Menu.Çorbalar></card>
    <card Title="Ana Yemekler" :SomeArray=Isletme.Menu.AnaYemekler></card>
    <card Title="Meşrubatlar" :SomeArray=Isletme.Menu.Meşrubatlar></card>
    <card Title="Tatlilar" :SomeArray=Isletme.Menu.Tatlilar></card>
    </div>
  </form>
</template>

<script>
import { ToggleButton } from "vue-js-toggle-button";
import Card from "../../components/Isletmeler-Ayrintilar-Card.vue";

export default {
  name: "Isletmeler-Ayrintilar",
  data: () => {
    return {
      Ekle: false,
      Edit: true,
      Isletme: {},
      IsletmelerLoadOk: false,
    };
  },
  components: {
    ToggleButton,
    Card
  },
  created() {
    window.Vue = this;
    if (this.$route.params.Id == "Ekle") {
      this.Ekle = true;
      this.Edit = false;
    } else {
      this.Ekle = false;
      this.ModeliIsle();
    }
  },
  methods: {
    async ModeliIsle() {
      await fetch(process.env.VUE_APP_SERVER + this.$route.params.Id)
        .then(res => res.json())
        .then(data => {
          this.Isletme = data;
          this.IsletmelerLoadOk = true;
        })
        .catch(() => {
          alert("Böyle bir işletme yok");
          this.$router.push("/Isletmeler");
        });
    },

    Kaydet() {
      const metod = this.Ekle ? "POST" : "PUT";
      const endpoint = this.Ekle ? "" : this.$route.params.Id;

      fetch(process.env.VUE_APP_SERVER + endpoint, {
        method: metod,
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(this.Isletme)
      })
        .then(res => {
          if (res.ok) {
            this.updateIsletmelerData();
            this.$router.push("/Isletmeler");
          } else res.json();
        })
        .then(data => (data ? alert(data) : ""));
    },

    Sil() {
      fetch(process.env.VUE_APP_SERVER + this.$route.params.Id, {
        method: "DELETE"
      }).then(res => {
        if (res.ok) {
          this.updateIsletmelerData();
          this.$router.push("/Isletmeler");
        } else {
          console.log(res);
        }
      });
    }
  }
};
</script>

